
* {
  margin: 0;
  padding: 0;
}
ul, ol {
  list-style: none;
}
a {
  text-decoration: none;
  color: #333;
}
.banner {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 1.2rem;
  background: #333;
  text-align: center;
  line-height: 1.2rem;
}
.banner span {
    font-size: 0.8rem;
    color: yellow;
}
nav.bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.33333rem;
  background: #333;
}
nav.bottom ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 1.33333rem;
}
nav.bottom ul li {
      width: 25%;
      height: 1.33333rem;
      text-align: center;
}
nav.bottom ul li span {
        font-size: 0.66667rem;
        color: #fff;
}
nav.bottom ul li p {
        font-size: 0.4rem;
        color: #fff;
}
nav.bottom ul li.xz span {
        color: yellow;
}
nav.bottom ul li.xz p {
        color: yellow;
}

* {
  margin: 0;
  padding: 0;
}
ul, ol {
  list-style: none;
}
a {
  text-decoration: none;
  color: #333;
}
.index {
  width: 9.46667rem;
  margin: 1.33333rem auto;
}
.index .play img {
    width: 100%;
    height: 6.66667rem;
}
.index .play /deep/ .swiper-pagination-bullet {
    width: 0.26667rem;
    height: 0.26667rem;
}
.index #picScroll {
    width: 9.46667rem;
    margin-top: 0.26667rem;
}
.index #picScroll .bd {
      width: 100%;
      height: 5.33333rem;
}
.index #picScroll .bd ul {
        float: left;
        width: 100%;
        padding-top: 10px;
}
.index #picScroll .bd ul li {
          width: 3.15556rem;
          float: left;
          text-align: center;
}
.index #picScroll .bd ul li img {
            width: 90%;
            height: 4rem;
}
.index #picScroll .hd {
      height: 0.8rem;
      line-height: 0.8rem;
      border-bottom: 0.02667rem solid #80BD6D;
      background: #f6f6f6;
      overflow: hidden;
      text-align: left;
      padding: 0 0.13333rem;
}
.index #picScroll .hd .on {
        background: #80BD6D;
}
.index #picScroll .hd ul {
        float: right;
        padding-top: 0.32rem;
}
.index #picScroll .hd ul li {
          float: left;
          width: 0.10667rem;
          height: 0.10667rem;
          background: #D0D0D0;
          margin: 0 0.06667rem;
          overflow: hidden;
          border-radius: 0.10667rem;
}
.index .story {
    width: 100%;
    margin-bottom: 2rem;
}
.index .story .zuopin {
      line-height: 0.8rem;
      font-size: 0.48rem;
      border-bottom: 0.04rem dashed #999;
      margin-bottom: 0.26667rem;
}
.index .story .zuopin .title {
        float: left;
        margin-right: 0.53333rem;
        color: palevioletred;
        font-weight: bold;
}
.index .story .zuopin .con {
        color: #666;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
}
